import { useContext, useRef, useEffect } from 'react'
import { observer } from 'mobx-react-lite'
import { Box, Stack, Fade } from '@mui/material'
import { RootStoreContext } from '../../store/rootStoreContext'
import QuoteArea from '../quoteArea'
import Slider from '../slider'
import Attribution from '../attribution'
import HealthBar from '../healthBar'
import { Switch, Case } from '../helper/switch'
import TutStage1PresolveTop from './tutStage1PresolveTop'
import TutStage1PresolveBottom from './tutStage1PresolveBottom'
import TutStage1PostsolveBottom from './tutStage1PostsolveBottom'
import TutStage2PresolveTop from './tutStage2PresolveTop'
import TutStage2PresolveBottom from './tutStage2PresolveBottom'
import TutStage2PostsolveBottom from './tutStage2PostsolveBottom'
import TutStage3PresolveTop from './tutStage3PresolveTop'
import TutStage3PresolveBottom from './tutStage3PresolveBottom'
import TutStage3PostsolveBottom from './tutStage3PostsolveBottom'

const Tutorial = (props) => {
  const [root] = useContext(RootStoreContext)
  const topTextRef = useRef()

  useEffect(() => {
    root.puzzleStore.loadPuzzleById('tut1')
  }, [root.puzzleStore])

  const getSliderYTranslate = () => {
    if (root.gameStore.puzzleState === 'succeeded') {
      return 0
    } else {
      return topTextRef.current?.offsetHeight
    }
  }

  const tutorialSkipCallback = () => {
    console.log('skip')
  }

  const tut1CompletedCallback = () => {
    root.puzzleStore.loadPuzzleById('tut2')
  }

  const tut2CompletedCallback = () => {
    root.puzzleStore.loadPuzzleById('tut3')
  }

  const tut3CompletedCallback = () => {
    root.gameStore.setInTutorial(false)
  }

  const stackStyle = {
    width: '100%',
    margin: '0 auto',
    alignItems: 'center',
  }

  const textPanelStyle = {
    width: '100%',
    textAlign: 'center',
    fontSize: '15pt',
    lineHeight: 'normal',
  }

  const topTextStyle = {
    position: 'absolute',
    width: ['70%', '55%', '40%'],
    pb: 3,
  }

  const bottomTextStyle = {
    pt: 1,
    px: 2,
    transition:
      root.gameStore.puzzleState === 'succeeded'
        ? 'opacity 1700ms ease-in-out'
        : '',
    opacity: 1,
  }

  const tutPage = root.puzzleStore.current?.id

  return (
    <Stack sx={stackStyle}>
      <Fade
        appear={false}
        in={root.gameStore.puzzleState !== 'succeeded'}
        timeout={300}
      >
        <Box id='toptext' ref={topTextRef} sx={[textPanelStyle, topTextStyle]}>
          <Switch test={tutPage}>
            <Case value='tut1'>
              <TutStage1PresolveTop />
            </Case>
            <Case value='tut2'>
              <TutStage2PresolveTop />
            </Case>
            <Case value='tut3'>
              <TutStage3PresolveTop />
            </Case>
          </Switch>
        </Box>
      </Fade>

      <Slider
        translateAmount={getSliderYTranslate()}
        isTranslated={root.gameStore.puzzleState !== 'succeeded'}
      >
        <Box
          id='hudTop'
          sx={{
            display: tutPage === 'tut3' ? 'flex' : 'none',
            flexDirection: 'row',
            justifyContent: 'right',
            flexWrap: 'nowrap',
            width: '100%',
          }}
        >
          <HealthBar sx={{ pr: '4px' }} health={root.gameStore.health} />
        </Box>

        <QuoteArea />
        <Attribution
          show={root.gameStore.puzzleState === 'succeeded'}
          html={root.gameStore.attributionHtml}
        />

        {/* before solve */}
        {root.gameStore.puzzleState !== 'succeeded' && (
          <Box id='bottomtext' sx={[textPanelStyle, bottomTextStyle]}>
            <Switch test={tutPage}>
              <Case value='tut1'>
                <TutStage1PresolveBottom skipCallback={tutorialSkipCallback} />
              </Case>
              <Case value='tut2'>
                <TutStage2PresolveBottom />
              </Case>
              <Case value='tut3'>
                <TutStage3PresolveBottom />
              </Case>
            </Switch>
          </Box>
        )}

        {/* after solve */}
        {root.gameStore.puzzleState === 'succeeded' && (
          <Fade appear={true} in={true} timeout={1000}>
            <Box
              id='bottomtext-postsolve'
              sx={[textPanelStyle, bottomTextStyle]}
            >
              <Switch test={tutPage}>
                <Case value='tut1'>
                  <TutStage1PostsolveBottom
                    buttonCallback={tut1CompletedCallback}
                  />
                </Case>
                <Case value='tut2'>
                  <TutStage2PostsolveBottom
                    buttonCallback={tut2CompletedCallback}
                  />
                </Case>
                <Case value='tut3'>
                  <TutStage3PostsolveBottom
                    buttonCallback={tut3CompletedCallback}
                  />
                </Case>
              </Switch>
            </Box>
          </Fade>
        )}
      </Slider>
    </Stack>
  )
}

export default observer(Tutorial)
