import { makeAutoObservable } from 'mobx'

export class Puzzle {
  attribution
  startingHealth
  id
  lang
  positions
  propnouns
  tags
  text
  ver
  wordList
  originalWordList
  category = ''

  constructor(obj) {
    this.setPuzzleData(obj)
    this.updateHyperlinks()
    this.generateWordList({ fromPositions: true })
    this.savePropNounsAsStrings()

    makeAutoObservable(this, {
      isInCorrectLocation: false,
      getCountOfCorrectWords: false,
    })
  }

  setPuzzleData(obj) {
    this.attribution = obj.attribution
    this.startingHealth = obj.health
    this.id = obj.id
    this.lang = obj.lang
    this.positions = obj.positions
    this.propnouns = obj.propnouns
    this.tags = obj.tags
    this.text = obj.text
    this.ver = obj.ver
  }

  updateHyperlinks() {
    this.attribution = this.attribution.replace(
      '<a href=',
      "<a target='_blank' href="
    )
  }

  generateWordList() {
    const puncRegex = /[.;:?,]+/g
    const txtWithSpaces = this.text.replace(puncRegex, ' $&')
    this.wordList = txtWithSpaces.split(' ')

    this.originalWordList = [...this.wordList].map((x) => x.toLowerCase())

    const positionedList = []
    for (let iPos = 0; iPos < this.positions.length; iPos++) {
      positionedList.push(this.wordList[this.positions[iPos]])
    }

    this.wordList = positionedList
  }

  savePropNounsAsStrings() {
    this.propnounStrings = []
    for (let i of this.propnouns) {
      this.propnounStrings.push(this.originalWordList[i])
    }
  }

  isInCorrectLocation(word, index) {
    return this.originalWordList[index] === word.trim().toLowerCase()
  }

  getCountOfCorrectWords(words) {
    let correctCount = 0
    for (let i = 0; i < words.length; i++) {
      const correct = this.isInCorrectLocation(words[i], i)
      correctCount += correct ? 1 : 0
    }

    return correctCount
  }

  isProperNoun(word) {
    return this.propnounStrings.includes(word)
  }
}
