import { useContext } from 'react'
import { observer } from 'mobx-react-lite'
import {
  Drawer,
  List,
  ListItem,
  ListItemButton,
  ListItemText,
  ListItemIcon,
  Typography,
  IconButton,
} from '@mui/material'
import DisabledByDefaultOutlinedIcon from '@mui/icons-material/DisabledByDefaultOutlined'
import SendIcon from '@mui/icons-material/Send'
import RefreshIcon from '@mui/icons-material/Refresh'
import { RootStoreContext } from '../store/rootStoreContext.js'

const CustomDrawer = (props) => {
  const [root] = useContext(RootStoreContext)

  const listItems = [
    {
      text: 'New puzzle',
      icon: <RefreshIcon />,
      handler: () => root.puzzleStore.loadRandomPuzzle(),
      enabled: !root.gameStore.inTutorial,
    },
    {
      text: 'Goto puzzle',
      icon: <RefreshIcon />,
      handler: () => {
        const id = prompt('Puzzle id?', '')
        root.puzzleStore.loadPuzzleById(id)
      },
      enabled: !root.gameStore.inTutorial,
    },
    {
      text: 'Run tutorial',
      icon: <SendIcon />,
      handler: () => {
        root.gameStore.setInTutorial(true)
      },
      enabled: !root.gameStore.inTutorial,
    },
    {
      text: 'Toggle InstaSolve',
      icon: <SendIcon />,
      handler: () => root.gameStore.toggleInstaSolve(),
      enabled: true,
    },
  ]

  return (
    <Drawer
      id='drawyer'
      variant='temporary'
      anchor='bottom'
      sx={{
        width: '15%',
        minWidth: '250px',
        flexShrink: 0,
      }}
      PaperProps={{
        sx: {
          width: '15%',
          minWidth: '250px',
          pt: 4,
          mb: 8,
        },
      }}
      open={root.gameStore.menuDrawerOpen}
      onClose={() => root.gameStore.toggleDrawerOpen()}
    >
      <IconButton
        size='large'
        onClick={() => root.gameStore.toggleDrawerOpen()}
        color='inherit'
        aria-label='menu'
        sx={{ position: 'absolute', top: 0, right: 0 }}
      >
        <DisabledByDefaultOutlinedIcon />
      </IconButton>
      <List>
        {listItems.map((item) => (
          <ListItem
            key={item.text}
            disablePadding
            onClick={() => {
              item.handler()
              root.gameStore.toggleDrawerOpen()
            }}
          >
            <ListItemButton enabled={`${item.enabled}`}>
              <ListItemIcon>{item.icon}</ListItemIcon>
              <ListItemText primary={item.text} />
            </ListItemButton>
          </ListItem>
        ))}
      </List>
      <Typography align='right' sx={{ p: 2, color: ['red', 'blue', 'green'] }}>
        v0016 / {root.puzzleStore.current?.id}
      </Typography>
    </Drawer>
  )
}

export default observer(CustomDrawer)
