import React, { useState, useEffect } from 'react'
import { useContext } from 'react'
import ReactGA from 'react-ga4'
import { Box, Stack, Container } from '@mui/material'
import { useTheme } from '@mui/material/styles'
import { observer } from 'mobx-react-lite'
import { RootStoreContext } from './store/rootStoreContext'
import Drawer from './components/drawer'
import HelpDialog from './components/helpDialog'
import SettingsDialog from './components/settingsDialog'
import GameBoard from './components/gameBoard'
import Tutorial from './components/tutorial/tutorial'
import Splash from './components/splash'
import MyAppBar from './components/AppBar'
import BannerAd from './components/bannerAd'

const App = () => {
  const [root] = useContext(RootStoreContext)
  //  const [drawerOpen, setDrawerOpen] = useState(false)
  const [settingsOpen, setSettingsOpen] = useState(false)
  const [helpOpen, setHelpOpen] = useState(false)
  const theme = useTheme()

  useEffect(() => {
    ReactGA.initialize('G-7S0H9Q0468')
  }, [])

  return (
    <Container maxWidth='sm'>
      <Box
        id='top-layout'
        sx={{
          color: theme.palette.primary.main,
          position: 'relative',
          height: '100vh',
          mt: '62px',
        }}
      >
        <BannerAd />
        <Box
          id='aqua'
          sx={{
            display: 'flex',
            height: `100vh`,
            background: 'radial-gradient(circle at center, #fff 0, #bbb 100%)',
          }}
        >
          {root.gameStore.showSplash ? (
            <Splash />
          ) : (
            <GameBoard id='gameboard' />
          )}
        </Box>

        <Drawer />

        <SettingsDialog
          open={settingsOpen}
          closeHandler={() => setSettingsOpen(false)}
        />

        <HelpDialog open={helpOpen} closeHandler={() => setHelpOpen(false)} />
      </Box>
      <MyAppBar
        openSettingsCallback={() => setSettingsOpen(true)}
        openHelpCallback={() => setHelpOpen(true)}
      />
    </Container>
  )
}

export default observer(App)
