import { Box } from '@mui/material'

const Attribution = (props) => {
  const attributionStyle = {
    width: '100%',
    px: '5%',
    mt: 1,
    fontFamily: 'Roboto',
    lineHeight: 'normal',
    transition: 'opacity 1000ms ease-in-out',
    opacity: props.show ? 1 : 0,
    pointerEvents: props.show ? 'auto' : 'none',
    // don't want attribution to impact slider size calc when hidden
    // using display prop prevents transition from running
    fontSize: props.show ? ['13pt', '15pt', '17pt'] : '0.1pt',
    textAlign: 'center',
  }

  const getHtml = () => {
    return { __html: props.html }
  }

  return (
    <Box
      id='attribution'
      sx={attributionStyle}
      dangerouslySetInnerHTML={getHtml()}
    ></Box>
  )
}

export default Attribution
