import { makeAutoObservable } from 'mobx'

export class GameStore {
  rootStore
  puzzleState = ''
  startingHealth = 0
  health = 0
  movesMade = 0
  instaSolve = true
  inTutorial = false
  tutPage = 'tut1'
  attributionHtml = ''
  adSpaceHeight = 60
  showSplash = false
  menuDrawerOpen = false
  mainWidth = 500

  constructor(rootStore) {
    this.rootStore = rootStore

    makeAutoObservable(this, {
      setMainWidth: false,
    })

    window.addEventListener('resize', this.setMainWidth)
    this.setMainWidth()
  }

  setMainWidth(ev) {
    //    this.mainWidth = Math.min(500, window.screenX)
  }

  setPuzzleState(state) {
    if (!['puzzleLoaded', 'playing', 'succeeded'].includes(state)) {
      throw new Error(`invalid state: ${state}`)
    }
    this.puzzleState = state
  }

  setStartingHealth(health) {
    this.startingHealth = health
    this.health = health
    this.movesMade = 0
  }

  setHealth(health) {
    this.health = health
  }

  decrementHealth() {
    this.health = Math.max(0, this.health - 1)
  }

  incrementMoveCount() {
    this.movesMade += 1
  }

  toggleInstaSolve(v) {
    this.instaSolve = !this.instaSolve
  }

  setAttributionHtml(v) {
    this.attributionHtml = v
  }

  setInTutorial(v) {
    this.inTutorial = v
  }

  toggleDrawerOpen() {
    this.menuDrawerOpen = !this.menuDrawerOpen
  }
}
